import { rangeLowerBound } from '../interpreter/binarySearch';
export class ColumnBinarySearch {
    constructor(dependencyGraph, config) {
        this.dependencyGraph = dependencyGraph;
        this.config = config;
    }
    // eslint-disable-next-line @typescript-eslint/no-unused-vars 
    add(value, address) { }
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    remove(value, address) { }
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    change(oldValue, newValue, address) { }
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    addColumns(columnsSpan) { }
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    removeColumns(columnsSpan) { }
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    removeSheet(sheetId) { }
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    moveValues(sourceRange, toRight, toBottom, toSheet) { }
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    removeValues(range) { }
    destroy() { }
    find(key, range, sorted) {
        if (range.height() < this.config.vlookupThreshold || !sorted) {
            const values = this.computeListOfValuesInRange(range);
            const index = values.indexOf(key);
            return index < 0 ? index : index + range.start.row;
        }
        else {
            return rangeLowerBound(range, key, this.dependencyGraph);
        }
    }
    advancedFind(keyMatcher, range) {
        const values = this.computeListOfValuesInRange(range);
        for (let i = 0; i < values.length; i++) {
            if (keyMatcher(values[i])) {
                return i + range.start.row;
            }
        }
        return -1;
    }
    computeListOfValuesInRange(range) {
        const values = [];
        for (const cellFromRange of range.addresses(this.dependencyGraph)) {
            const value = this.dependencyGraph.getScalarValue(cellFromRange);
            values.push(value);
        }
        return values;
    }
}
