import { CellError, ErrorType } from '../../Cell';
import { AstNodeType } from '../../parser';
import { FunctionPlugin } from './FunctionPlugin';
export class ErrorFunctionPlugin extends FunctionPlugin {
    erf(ast, formulaAddress) {
        if (ast.args.length < 1 || ast.args.length > 2) {
            return new CellError(ErrorType.NA);
        }
        if (ast.args.some((ast) => ast.type === AstNodeType.EMPTY)) {
            return new CellError(ErrorType.NUM);
        }
        const lowerBound = this.getNumericArgument(ast, formulaAddress, 0);
        if (lowerBound instanceof CellError) {
            return lowerBound;
        }
        if (ast.args.length === 2) {
            const upperBound = this.getNumericArgument(ast, formulaAddress, 1);
            if (upperBound instanceof CellError) {
                return upperBound;
            }
            return erf2(lowerBound, upperBound);
        }
        return erf(lowerBound);
    }
    erfc(ast, formulaAddress) {
        return this.templateWithOneCoercedToNumberArgument(ast, formulaAddress, (arg) => {
            return erfc(arg);
        });
    }
}
ErrorFunctionPlugin.implementedFunctions = {
    'ERF': {
        method: 'erf',
    },
    'ERFC': {
        method: 'erfc',
    },
};
function erf(x) {
    if (x >= 0) {
        return erfApprox(x);
    }
    else {
        return -erfApprox(-x);
    }
}
function erfApprox(x) {
    const polyExponent = 16;
    const coefficients = [
        0.0705230784,
        0.0422820123,
        0.0092705272,
        0.0001520143,
        0.0002765672,
        0.0000430638,
    ];
    const poly = coefficients.reduce((acc, coefficient, index) => {
        return acc + coefficient * Math.pow(x, index + 1);
    }, 1);
    return 1 - (1 / Math.pow(poly, polyExponent));
}
function erf2(lowerBound, upperBound) {
    return erf(upperBound) - erf(lowerBound);
}
function erfc(x) {
    return 1 - erf(x);
}
