import { CellError, ErrorType } from '../../Cell';
import { FunctionPlugin } from './FunctionPlugin';
export class BitwiseLogicOperationsPlugin extends FunctionPlugin {
    bitand(ast, formulaAddress) {
        return this.templateWithTwoPositiveIntegerArguments(ast, formulaAddress, (left, right) => {
            return left & right;
        });
    }
    bitor(ast, formulaAddress) {
        return this.templateWithTwoPositiveIntegerArguments(ast, formulaAddress, (left, right) => {
            return left | right;
        });
    }
    bitxor(ast, formulaAddress) {
        return this.templateWithTwoPositiveIntegerArguments(ast, formulaAddress, (left, right) => {
            return left ^ right;
        });
    }
    templateWithTwoPositiveIntegerArguments(ast, formulaAddress, fn) {
        const validationResult = this.validateTwoNumericArguments(ast, formulaAddress);
        if (validationResult instanceof CellError) {
            return validationResult;
        }
        const [coercedLeft, coercedRight] = validationResult;
        if (coercedLeft < 0 || coercedRight < 0 || !Number.isInteger(coercedLeft) || !Number.isInteger(coercedRight)) {
            return new CellError(ErrorType.NUM);
        }
        return fn(coercedLeft, coercedRight);
    }
}
BitwiseLogicOperationsPlugin.implementedFunctions = {
    'BITAND': {
        method: 'bitand',
    },
    'BITOR': {
        method: 'bitor',
    },
    'BITXOR': {
        method: 'bitxor',
    },
};
